<template >
  <div class="course-grid" :body-style="{ padding: '5px' }" style="min-height: 50px">
    <course-tag v-for="course in coursesInGrid"
                :key="course.cname + d + course.isPreview"
                :course="course"
                :course-size="courseSize"
                :background-color="backgroundColor"/>
    <course-query class="hidden-md-and-down"
                  v-if="showCourseInput"
                  v-show="isEditable"
                  ref="saveTagInput"
                  :course-size="courseSize"
                  :position="position"
                  :show-course-input.sync="showCourseInput"
    />
    <el-button class="hidden-md-and-down button-new-tag"
               v-else
               v-show="isEditable"
               size="mini"
               :body-style="{ padding: '5px' }"
               @click="showQueryInput">+
    </el-button>
    <el-button class="hidden-lg-and-up"
               v-show="coursesInGrid.length === 0"
               size="mini"
               @click="promptCourses"> +</el-button>
    <el-dialog title="搜尋課程" :visible.sync="showPromptCourseInput">
      包含{{ d + this.$store.state.timetableOptions.timeslot[this.scope.$index] }}的課程
      <course-query :course-size="courseSize"
                    :position="position"
                    :show-course-input.sync="showCourseInput"
                    :show-prompt-course-input.sync="showPromptCourseInput"
      />
      <div slot="footer" class="dialog-footer">
        <el-button @click="showPromptCourseInput = false">取消</el-button>
        <el-button type="primary" @click="showPromptCourseInput = false">新增</el-button>
      </div>
    </el-dialog>
  </div>

</template>
<script>

import CourseTag from "@/components/Element/CourseGrid/CourseTag";
import CourseQuery from "@/components/Element/CourseGrid/CourseQuery";

export default {
  name: 'course-grid',
  components: {CourseQuery, CourseTag},
  props: {
    d: {},
    scope: {}
  },
  data() {
    return {
      showCourseInput: false,
      inputValue: '',
      showPromptCourseInput: false
    }
  },
  computed: {
    coursesInGrid() {
      return this.scope.row[this.d].filter((e) => (e !== null))
    },
    courseSize() {
      return this.courseOption.allowCrash ? "mini" : "large"
    },
    position() {
      return this.d + this.courseOption.timeslot[this.scope.$index]
    },
    isEditable() {
      let notFocusOn = !this.courseView.addingCourseGrid
      let focusOnMe = this.courseView.addingCourseGrid === this.position
      let courseNotFilled = this.coursesInGrid.length === 0;
      return ((notFocusOn || focusOnMe) &&
          (this.courseOption.allowCrash || courseNotFilled) )
    },
  },
  methods: {
    promptCourses() {
      this.showPromptCourseInput = true
    },
    showQueryInput() {
      this.courseView.addingCourseGrid = this.position
      this.showCourseInput = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.courseQuery.$refs.input.focus();
      });
    },
    onPromptCourseSelected(course) {
      this.updateCourse(course);
      this.$store.state.timetableOptions.previewCourse = null
      this.$nextTick(() => {
        this.showCourseInput = false;
        this.inputValue = '';
        this.showPromptCourseInput = false
      });
    },
    backgroundColor(course) {
      if (!course) return '';
      return {
        '必修': 'warning',
        '選修': '',
        '通識': 'danger',
      }[course.type]
    }
  }
}
</script>
