.course-grid {
  height: 80px;
}

.popover-course {
  width: 300px
}

.course-tag {
  margin-left: 0px;
  margin-right: 0px;
}

.button-new-tag {
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 10px;
  padding-right: 10px;
  height: 24px;
  vertical-align: bottom;
}

.input-new-tag {
  font-size: 8px;
  margin-left: 10px;
  margin-right: 10px;
  vertical-align: bottom;
  width: 100%;
}

