export function newCourseSlots(generator) {
    const weekdays = '1234567'.split('')
    const time = 'MNABCDEXEFGHYIJKL'.split('')
    var courseSlots = {}
    for (const d of weekdays) {
        for (const t of time) {
            courseSlots[d + t] = generator()
        }
    }
    return courseSlots
}


