export function addCourse(courseGroup, course) {
    if (!course) return courseGroup;
    let courseAddedBefore = courseGroup.listID.includes(course.id)
    if (!courseAddedBefore) {
        courseGroup.list.push(course)
        courseGroup.listID.push(course.id)
        course.timeSchedules.forEach((t) => courseGroup.scheduleSlots[t].push(course))
        course.timeSchedules.forEach((t) => courseGroup.slotsFilled[t] = true)
    } else {
        console.log(courseGroup, course)
        let courseIndex = courseGroup.list.indexOf(course)
        courseGroup.list.splice(courseIndex, 1)
        let courseIDIndex = courseGroup.listID.indexOf(course.id)
        courseGroup.listID.splice(courseIDIndex, 1)
        for (let slot of course.timeSchedules) {
            courseGroup.scheduleSlots[slot].splice(course)
            if (courseGroup.scheduleSlots[slot].length === 0) {
                courseGroup.slotsFilled[slot] = false
            }
        }
    }
    return courseGroup
}

export function findDupNameCourses(courses) {
    // console.log(courses)
    var coursesNameEnum = {}
    const fuckingEnglish = []
    for (let c of courses) {
        var lang = c['授課語言代碼']
        var name = c.cname
        if(lang === 'en-us'){
            fuckingEnglish.push(c)
            continue
        }
        if(!coursesNameEnum[name]){
            coursesNameEnum[name] = []
        }
        coursesNameEnum[name].push(c)
    }

    for(const c of fuckingEnglish){
        for(const cname of Object.keys(coursesNameEnum)){
            if(c.cname.includes(cname)){
                coursesNameEnum[cname].push(c)
                break
            }
        }
    }

    var dupCourses = []

    var courseNameGroup = Object.values(coursesNameEnum)
    var nonUniqueCourse = courseNameGroup.filter((cs) => cs.length > 1)
    for(const cs of nonUniqueCourse){
        dupCourses = dupCourses.concat(cs)
    }
    var uniqueCourses = []
    var uniqueCourseRaw = courseNameGroup.filter((cs) => cs.length === 1)
    for(const cs of uniqueCourseRaw){
        uniqueCourses = uniqueCourses.concat(cs)
    }
    return {uniqueCourses, dupCourses}
}

export function findDepGroup(deps) {
    let depGroupType = {
        '在職專班': {cname: '在職專班', child: [], keyword: '專班'},
        '碩士班': {cname: '碩士班', child: [], keyword: '碩士'},
        '博士班': {cname: '博士班', child: [], keyword: '博士'},
        '研究所': {cname: '研究所', child: [], keyword: '研究所'},
        '跨域學程': {cname: '跨域學程', child: [], keyword: '跨域學程'},
        '學分學程': {cname: '學分學程', child: [], keyword: '學程'},
        '大學部': {cname: '大學部', child: [], keyword: '系'},
        '其他': {cname: '其他', child: [], keyword: ''},
    }
    for (let d of deps) {
        let depName = d.cname
        for (let dg in depGroupType) {
            // console.log(dg)
            let dgKeyword = depGroupType[dg].keyword
            if (depName.includes(dgKeyword)) {
                depGroupType[dg].child.push(d)
                break
            }
        }
    }
    return depGroupType
}

export function findDepEnum(courses) {
    // console.log(courses)
    let depNameEnum = {}
    for (let c of courses) {
        c.leaf = false
        let depName = c.dep_cname
        if (!depNameEnum[depName]) {
            depNameEnum[depName] = {'cname': depName, 'child': []}
        }
        depNameEnum[depName].child.push(c)
    }
    return depNameEnum
}

export function parseCourse(courseInput){
    courseInput += '\n0000'
    const courseLinePattern = /\d{4}.+?(?=\d{4})/gs;
    const courses = courseInput.matchAll(courseLinePattern)
    // Print(courses)
    const parsedCourse = {}
    for(let c of courses){
        // Print(c[0])
        // Print(c)
        let cp = c[0].replaceAll('\t',' | ').replace('\n','')
        // Print(cp)
        const attributes = cp.split(' | ')
        let attrName = '當期課號 | 開課單位 | 備註 | 課名 | 人上限 | 已登記 | 志願1 | 志願2 | 志願3 | 志願4 | 志願5 | 時間地點 | 學分 | 時數 | 老師 | 類別 | 備註'.split(' | ')
        let courseAttr = {}
        for(let attrIndex of Array(attributes.length).keys()){
            courseAttr[attrName[attrIndex]] = attributes[attrIndex]
        }
        if(courseAttr['人上限'] === '不限' ) courseAttr['人上限'] = 9999;
        courseAttr['剩餘空位'] = parseInt(courseAttr['人上限']) - parseInt(courseAttr['已登記'])
        courseAttr['第一志願機率'] = Math.round(courseAttr['剩餘空位'] * 100 / (parseInt(courseAttr['志願1']) + 1))
        parsedCourse[attributes[0]] = courseAttr
    }
    // Print(parsedCourse)
    return parsedCourse
}
